<template>
  <div>
    <transition name="fade">
      <div class="list-mask" v-show="listShow" @click="openList"></div>
    </transition>
    <div class="shopCart">
      <div class="content" @click="openList">
        <div class="content-left">
          <div class="logo-wrapper">
            <div class="logo" :class="{'highLight':totalCount > 0}">
              <i class="icon-shopping_cart" :class="{'highLight':totalCount > 0}">
              </i>
            </div>
            <div class="num" v-show="totalCount>0">
              {{totalCount}}
            </div>
          </div>
          <div class="price" :class="{'highLight':totalPrice>0}">￥{{totalPrice}}</div>
          <div class="decs">另需配送费￥{{deliveryPrice}}元</div>
        </div>
        <div class="content-right" :class="payClass">
          <div class="pay" @click.stop="payTo">
            {{payDesc}}
          </div>
        </div>
      </div>
      <div class="ball-container">
        <transition v-for="(ball,index) in balls" @before-enter="beforeEnter"
                    @enter="enter"
                    @after-enter="afterEnter" name="drop" :key="index">
          <div v-show="ball.show" class="ball">
            <div class="inner inner-hook">
              1
            </div>
          </div>
        </transition>
      </div>
      <transition name="showCart">
        <div class="shopCart-list" v-show="listShow">
          <div class="listHeader">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty"> 清空</span>
          </div>
          <div class="listContent" ref="listContent">
            <ul>
              <li class="food" v-for="food in selectFoods">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>￥{{food.price * food.count}}</span>
                </div>
                <div class="cartControl-wrapper">
                  <cartControl :food="food" :bus="bus"></cartControl>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
  </div>

</template>

<script>
  /* eslint-disable no-unused-vars,spaced-comment,no-new */
  import BScroll from "better-scroll"
  import Velocity from "velocity-animate"
  import cartControl from "@/components/cartControl/cartControl.vue"
  export default {
    name: 'shopCart',
    data () {
      return {
        balls: [{
          show: false
        },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          }],
        dropIndex: 0,
        fold: true
      }
    },
    props: {
      selectFoods: {
        type: Array,
        default(){
          return [
            {
              price: 0,
              count: 0
            }
          ];
        }
      },
      deliveryPrice: {
        type: Number,
        default: 0
      },
      minPrice: {
        type: Number,
        default: 0
      },
      bus: {
        type: Object
      }
    },
    computed: {
      totalPrice(){
        let total = 0;
        this.selectFoods.forEach(
          (food) => {
            total += food.price * food.count
          }
        )
        return total
      },
      totalCount(){
        let count = 0;
        this.selectFoods.forEach(
          (food) => {
            count += food.count
          }
        )
        return count;
      },
      payDesc(){
        if (this.totalPrice === 0) {
          return `￥${this.minPrice}起送`;
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice
          return `还差￥${diff}起送`;
        } else {
          return '去结算';
        }
      },
      payClass(){
        if (this.totalPrice < this.minPrice) {
          return 'not-enough'
        } else {
          return 'enough'
        }
      },
      listShow(){
        if (this.totalCount === 0) {
          this.fold = true
          return false
        } else {
          let show = !this.fold;
          if (show) {
            this.$nextTick(() => {
              if (!this.scroll) {         /*性能优化*/
                this.scroll = new BScroll(this.$refs.listContent, {
                  click: true
                })
              } else {
                this.scroll.refresh()
              }
            })
          }
          return show;
        }
      }
    },
    methods: {
      dropTo(data){
        this.balls[this.dropIndex].show = true;
        this.balls[this.dropIndex].el = data;
        this.dropIndexAdd();
        /*  for(let i = 0; i < this.balls.length;i++){
         if(!this.balls[i].show){
         this.dropBalls.push(this.balls[i]);
         this.balls[i].show = true;
         this.balls[i].el = data;
         break;
         }
         }*/
      },
      dropIndexAdd(){
        this.dropIndex++
        if (this.dropIndex === this.balls.length) {
          this.dropIndex = 0;
        }
      },
      empty(){
        this.selectFoods.forEach((food) => {
          food.count = 0
        })
      },
      payTo(){
        if (this.totalPrice < this.minPrice) {
          return
        }
        window.alert(`支付￥${this.totalPrice}`)
      },
      beforeEnter(el){
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect();  // 获得元素相对于视口的距离
            let x = rect.left - 44 + 'px';    //  计算x轴平移的距离   距底部宽度减去加号自身宽度
            let y = -(window.innerHeight - rect.top - 112) + 'px';  //   计算y轴平移的距离 底部宽度加上加号自身宽度
            el.style.webkitTransform = `translate3d(0,${y},0)`     //将小球位移到点击的位置
            el.style.transform = `translate3d(0,${y},0)`
            let inner = el.querySelectorAll(".inner-hook")[0]
            inner.style.webkitTransform = `translate3d(${x},0,0)`
            inner.style.transform = `translate3d(${x},0,0)`
          }
        }
      },
      enter(el, done){
        /* let refresh = el.offsetHeight;
         this.$nextTick(()=>{
         el.style.webkitTransform = 'translate3d(0,0,0)'
         el.style.transform = 'translate3d(0,0,0)'
         let inner = el.querySelectorAll(".inner-hook")[0]
         inner.style.webkitTransform = 'translate3d(0,0,0)'
         inner.style.transform = 'translate3d(0,0,0)'
         })*/
        Velocity(el, {transform: "translate3d(0,0,0)"}, {duration: 600}, "cubic-bezier(.49,-0.44,.83,.67)")
        let inner = el.querySelectorAll(".inner-hook")[0]
        Velocity(inner, {transform: "translate3d(0,0,0)"}, {duration: 600}, "linear")
        done();
      },
      afterEnter(el){
        /* console.log(ball);*/
        /* setTimeout(()=>{
         while(this.dropBalls){
         let ball = this.dropBalls.pop();
         if(ball.show){
         ball.show = false;
         }
         }
         }, 400)*/
        for (let i = 0; i < this.balls.length; i++) {
          this.balls[i].show = false
        }
        this.balls[this.dropIndex].show = false;
      },
      openList(e){
        e.stopPropagation()
        if (!e._constructed && !this.totalCount) {
          return
        }
        this.fold = !this.fold
      }
    },
    created(){
      /*优化体验  异步执行*/
      this.bus.$on("addCart", (data) => {
        this.$nextTick(() => {
          this.dropTo(data)
        })
      })
    },
    components: {
      cartControl
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only  添加scoped属性限制css只能在本模板用 -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/css/mixin.styl"
  @import "../../common/css/base.styl"
  .shopCart
    position: fixed
    left: 0
    bottom: 0
    z-index: 1
    width: 100%
    height: 92px
    .content
      display: flex
      background: #141d27
      height: 100%
      .content-left
        flex: 1
        .logo-wrapper
          position: relative
          display: inline-block
          top: -20px
          margin: 0 24px
          padding: 12px
          width: 112px
          height: 112px
          box-sizing: border-box
          vertical-align: top
          border-radius: 50%
          background: #141d27
          .logo
            display: inline-block
            width: 100%
            height: 100%
            border-radius: 50%
            background: #2b343c
            text-align: center
            &.highLight
              background: rgb(0, 160, 220)
            .icon-shopping_cart
              font-size: 48px
              line-height: 88px
              color: #80858a
              &.highLight
                color: #fff
          .num
            position: absolute
            top: 0
            right: 0
            width: 48px
            height: 32px
            line-height: 32px
            text-align: center
            border-radius: 32px
            font-size: 18px
            font-weight: 700
            color: #ffffff
            background: rgb(240, 20, 20)
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .4)
        .price
          display: inline-block
          vertical-align: top
          margin-top: 24px
          padding-right: 24px
          line-height: 48px
          box-sizing: border-box
          border-right: 1px solid rgba(255, 255, 255, .1)
          font-size: 32px
          font-weight: 700
          color: rgba(255, 255, 255, .4)
          &.highLight
            color: #fff
        .decs
          display: inline-block
          vertical-align: top
          margin: 24px 0 0 24px
          line-height: 48px
          font-size: 20px
          font-weight: 200
          color: rgba(255, 255, 255, .4)
      .content-right
        flex: 0 0 210px
        width: 210px
        .pay
          height: 96px
          line-height: 96px
          text-align: center
          font-size: 24px
          font-weight: 700
        &.not-enough
          background: #2b333b
          color: rgba(255, 255, 255, .4)
        &.enough
          background: #00b43c
          color: #ffffff

    .ball-container
      display: inline-block
      .ball
        position: fixed
        display: inline-block
        left: 64px
        bottom: 44px
        border-radius: 50%
        z-index: 100
        transition: all .6s cubic-bezier(.49, -1.29, .85, -0.2)
        .inner
          display: inline-block
          width: 36px
          height: 36px
          border-radius: 50%
          text-align: center
          line-height: 36px
          color: white;
          font-size: 20px
          font-weight: 700
          background: rgb(0, 160, 220)
          transition: all .6s linear

    .shopCart-list
      position: absolute
      left: 0
      top: 0
      z-index: -1 /*使底层购物车在上面*/
      width: 100%
      transform: translate3d(0, -100%, 0)
      transition: transform .5s ease-in-out
      &.showCart-enter, &.showCart-leave-active
        transform: translate3d(0, 0, 0)
      .listHeader
        height: 80px
        padding: 0 36px
        background: #f3f5f7
        border-bottom: 2px solid rgba(7, 17, 27, .1)
        line-height: 80px
        .title
          float: left
          font-size: 28px
          font-weight: 200
          color: rgb(7, 17, 27)
        .empty
          float: right
          font-size: 24px
          color: rgb(0, 160, 220)
      .listContent
        padding: 0 36px
        max-height: 434px
        overflow: hidden
        background: #ffffff
        .food
          position: relative
          padding: 24px 0
          height: 96px
          box-sizing: border-box
          border-1px(rgba(7, 17, 27, .1))
          .name
            line-height: 48px
            font-size: 28px
            color: rgb(7, 17, 27)
          .price
            position: absolute
            right: 180px
            bottom: 24px
            line-height: 48px
            font-size: 28px
            font-weight: 700
            color: rgb(240, 20, 20)
            margin-right: 12px
          .cartControl-wrapper
            position: absolute
            right: -12px
            top: 50%
            transform: translateY(-50%)

  .list-mask
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 100%
    opacity: 1
    backdrop-filter: blur(10px) /*背景模糊*/
    background: rgba(7, 17, 27, .6)
    transition: all .01s ease-in-out
    &.fade-enter, &.fade-leave-active
      opacity: 0
      background: rgba(7, 17, 27, 0)

</style>



