export function formateDate(date, fmt){     //  获得时间  时间格式
    if(/(y+)/.test(fmt)){
      fmt = fmt.replace(RegExp.$1,   //  选中取得正则中第一个括号的值
        (date.getFullYear() + '').substr(4 - RegExp.$1.length))//  匹配上一个正则
    }
    let o = {
      'M+' : date.getMonth() + 1,
      'd+' : date.getDate(),
      'h+' : date.getHours(),
      'm+' : date.getMinutes(),
      's+' : date.getSeconds()
    };
    for(let k in o){
          if(new RegExp(`(${k})`).test(fmt)){
            let str = o[k] + '';
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str))
          }
    }
    return fmt;
}
function padLeftZero(str){     //  若是个位数 就在前面加1
  return ('00' + str).substr(str.length);
}



