/* eslint-disable no-undef,comma-spacing */
import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import World from '@/components/World'
import goods from '@/components/goods/goods'
import seller from '@/components/seller/seller'
import ratings from '@/components/ratings/ratings'
import Login from '@/components/login'
  Vue.use(Router)
 export default new Router({
    mode: 'history',
    routes: [
    {
      path: '/ele/' , redirect:'/ele/hello'

    },{
        path:'/ele/login',
        name:'login',
        component:Login
    },{
      path:'/ele/hello',
      component:Hello,
      children:[
        {
          path:'/ele/hello/',redirect:'/ele/hello/goods'
        },
        {
          path:'/ele/hello/World',
          name:'World',
          component:World
        },{
          path:'/ele/hello/goods',
          name:'goods',
          component:goods
        },{
          path:'/ele/hello/seller',
          name:'seller',
          component:seller
        },{
          path:'/ele/hello/ratings',
          name:'ratings',
          component:ratings
        }
      ]
    }
  ],
   linkActiveClass:'active'
})



