<template>
    <div class="cartControl">
        <transition name="move">
          <div class="cart-decrease" v-show="food.count>0" @click="decreaseCart">
            <span class="inner icon-remove_circle_outline">

            </span>
          </div>
        </transition>
        <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
          <div class="cart-add icon-add_circle" @click="addCart"></div>
    </div>
</template>

<script>
  import Vue from 'vue';
  export default {
    name: '',
    data () {
      return {}
    },
    props:{
      food:{
        type:Object
      },
      bus:{
        type:Object
      }
    },
    created(){
    },
    methods:{
      addCart(e){
        e.stopPropagation();
        if(!e._constructed){
          return
        }
        if(!this.food.count){    // 如果没有count  就把count设为1
              Vue.set(this.food, 'count', 1)
        }else{
          this.food.count ++;
        }
        if(this.bus){
          this.bus.$emit("addCart", e.target)
        }
      },
      decreaseCart(e){
        e.stopPropagation();
        if(!e._constructed){
          return
        }
        this.food.count --;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only  添加scoped属性限制css只能在本模板用 -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
    .cartControl
      font-size:0
      .cart-decrease
        display:inline-block
        padding: 12px
        transition:all .4s linear
        .inner
          display:inline-block
          line-height: 48px
          font-size: 48px
          color:rgb(0,160,220)
          transition:all .4s linear
          transform:rotate(0)
        &.move-enter-active,&.move-leave
          opacity:1
          transform:translate3D(0,0,0)
        &.move-enter,&.move-leave-active
          opacity:0
          transform:translate3D(48px,0,0)
          .inner
            transform:rotate(180deg)
      .cart-count
        display:inline-block
        vertical-align:top
        width: 48px
        padding-top: 12px
        line-height: 48px
        text-align:center
        font-size: 20px
        color:rgb(147,153,159)
      .cart-add
        display:inline-block
        padding: 12px
        line-height: 48px
        font-size: 48px
        color:rgb(0,160,220)
</style>



