export default function () {
    let url = window.location.search;
    let obj = {};
    let reg = /[?&][^?&]+=[^?&]+/g
    let arr = url.match(reg);
    if(arr){
      arr.forEach((item) =>{
        let arr = item.substring(1).split("=");
        let key = arr[0];
        let value = arr[1];
        obj[key] = value;
      })
    }
    return obj;
}



