<template>
    <transition name="move">
      <div class="food" v-show="foodShowFlag" ref="food">
          <div class="food-content">
            <div class="image-header">
              <img  :src="food.image">
              <div class="back" @click="hide">
                <i class="icon-arrow_lift"></i>
              </div>
            </div>
            <div class="content">
              <h1 class="title">
                {{food.name}}
              </h1>
              <div class="detail">
                <span class="sell-count">
                  月售{{food.sellCount}}份
                </span>
                <span class="rating">
                  好评率{{food.rating}}%
                </span>
              </div>
              <div class="price">
                <span class="now">￥{{food.price}}</span>
                <span class="old" v-show="food.oldPrice">
                  ￥{{food.oldPrice}}
                </span>
                <div class="cartControl-wrapper" >
                  <cartControl :food = "food" :bus = "bus"></cartControl>
                </div>
                <transition name="fade" @after-enter="fadeAfterEnter" @before-leave="fadeBeforeLeave">
                  <div class="buy" v-show = "!food.count" @click="addFirst" >加入购物车</div>
                </transition>
              </div>
            </div>
            <split v-if="food.info"></split>
            <div class="info" v-if="food.info">
              <h1 class="title">
                商品介绍
              </h1>
              <p class="text">
                {{food.info}}
              </p>
            </div>
            <split></split>
            <div class="rating" ref="rating">
              <h1 class="title">
                商品评价
              </h1>
              <ratingSelect :select-type.sync = "selectType" :only-content = "onlyContent" :desc="desc"
                            :ratings="food.ratings"  @updateSelectType = "updateSelectType" @switchOnly="switchOnly">

              </ratingSelect>
              <div class="rating-wrapper">
                <ul v-show="food.ratings&&food.ratings.length">
                    <li v-for="rating in food.ratings" class="rating-item"
                    v-show = "needShow(rating.rateType,rating.text)">
                        <div class="user">
                          <span class="name">{{rating.username}}</span>
                          <img class="userImage" width="24" height="24" :src="rating.avatar">
                        </div>
                        <div class="time">
                          {{rating.rateTime | formateDate}}
                        </div>
                        <p class="text">
                          <i :class="{'icon-thumb_up':rating.rateType === 0,'icon-thumb_down':rating.rateType === 1}"></i>
                          {{rating.text}}
                        </p>
                    </li>
                </ul>
                <div class="no-ratings" v-show="!food.ratings || !food.ratings.length">
                    暂无评价
                </div>
              </div>
            </div>
          </div>
        </div>
    </transition>
</template>

<script>
  /* eslint-disable no-unused-vars */

  import BScroll from 'better-scroll'
  import Vue from 'vue'
  import cartControl from '@/components/cartControl/cartControl.vue'
  import split from '@/components/split/split.vue'
  import ratingSelect from '@/components/ratingSelect/ratingSelect.vue'
  import { formateDate } from '../../common/js/date.js'
  const ALL = 2;
  export default {
    name: '',
  components:{
    cartControl,
    split,
    ratingSelect
  },
    data () {
      return {
        foodShowFlag:false,
        selectType:ALL,
        desc:{
          all:'全部',
          ok: '推荐',
          no: '吐槽'
        },
        onlyContent:true
      }
    },
    props:{
        food:{
          type:Object
        },
      bus:{
            type:Object
      }
    },
    created(){
      this.$nextTick(()=>{

    })
    },
    methods:{
      fadeBeforeLeave(el){
        el.innerHTML = "";
      },
      fadeAfterEnter(el){
          el.innerHTML = "加入购物车"
      },
      show(){
        this.foodShowFlag = true;
        this.selectType = ALL;
        this.onlyContent = false;
        this.$nextTick(()=>{
          if(!this.foodScroll){
            let food = this.$refs.food
            this.foodScroll = new BScroll(food, {
              click:true
            })
          }else{
            this.foodScroll.refresh()
          }
        })
      },
      hide(){
        this.foodShowFlag = false
      },
      addFirst(e){
        if(!e._constructed){
          return
        }
        if(this.food.count > 0){
          this.food.count++
        }else{
          Vue.set(this.food, "count", 1)
        }
        this.bus.$emit("addCart", e.target)
      },
      updateSelectType(data){
        this.selectType = data;
        this.$nextTick(()=>{     //  dom更新后触发
          this.foodScroll.refresh();
        })
      },
      switchOnly(data){
        this.onlyContent = data
        this.$nextTick(()=>{
          this.foodScroll.refresh();
        })
      },
      needShow(type, text){
        if(this.onlyContent && !text){ //  判断是否店家只看有内容和评价  如果是过滤没有内容的
          return false;
        }
        if(this.selectType === ALL){ //  判断是否选择全部显示
          return true
        }else{
          return type === this.selectType
        }
      }
    },
    filters:{
      formateDate(time){    //  添加过滤器
          let date = new Date(time);    //  将时间戳转化为js内置对象date的类型
          return formateDate(date, 'yyyy-MM-dd hh:mm')
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only  添加scoped属性限制css只能在本模板用 -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/css/index.styl"
    .food
      position:fixed
      left:0
      top:0
      bottom: 92px
      width:100%
      background: #ffffff
      transition:all .2s linear
      transform:translate3d(0,0,0)
      .image-header
        position:relative
        width:100%
        height:0
        padding-top:100%
        img
          position:absolute
          top:0
          left:0
          width:100%
          height:100%
      .back
        position:absolute
        top: 12px
        left:0
        border-radius:50%
        background:rgba(0,0,0,.1)
        .icon-arrow_lift
          display:block
          padding: 20px
          font-size: 20px
          color: #ffffff
      .content
        padding: 36px
        .title
          line-height: 28px
          margin-bottom: 16px
          font-size: 28px
          font-weight: 700px
          color:rgb(7,17,27)
        .detail
          margin-bottom: 36px
          height: 20px
          line-height: 36px
          .sell-count,.rating
            font-size: 20px
            color:rgb(147,153,159)
          .sell-count
            margin-right: 24px
        .price
          position:relative
          font-weight:700
          line-height: 48px
          .now
            margin-right: 16px
            font-size: 28px
            color:rgb(240,20,20)
          .old
            text-decoration: line-through
            font-size: 20px
            color:rgb(147,153,159)
          .cartControl-wrapper
            float:right
            margin-top: -10px
          .buy
            position:absolute
            width: 148px
            height: 48px
            top:0
            right:0
            line-height: 48px
            margin-right: 12px
            padding:0 24px
            box-sizing:border-box
            font-size: 20px
            -webkit-border-radius: 12px
            -moz-border-radius: 12px
            border-radius: 12px
            color: #ffffff
            background:rgb(0,160,220)
            transition:all .5s ease-in-out
            &.fade-enter,&.fade-leave-active
              padding:0
              border-radius:50%;
              width: 48px
      .info
        padding: 32px
        .title
          line-height: 28px
          margin-bottom: 12px
          font-size: 28px
          color:rgb(7,17,27)
        .text
          line-height: 48px
          font-size: 24px
          color:rgb(77,85,93)
          font-weight:200
      .rating
        padding-top: 36px
        .title
          line-height: 28px
          margin-left: 36px
          font-size: 28px
          color:rgb(7,17,27)
        .rating-wrapper
          padding:0 36px
          .rating-item
            position:relative
            padding:32px 0
            border-1px(rgba(7,17,27,.1))
            .user
              position:absolute
              right:0
              top: 32px
              font-size:0
              line-height: 24px
              .name
                display:inline-block
                vertical-align:top
                margin-right: 12px
                font-size: 10px
                color:rgb(147,153,159)
              .userImage
                border-radius:50%
            .time
              line-height: 24px
              margin-bottom: 32px
              font-size: 20px
              color:rgb(147,153,159)
            .text
              line-height: 32px
              font-size: 24px
              color:rgb(7,17,27)
              .icon-thumb_up,icon-thumb_down
                margin-right: 8px
                font-size: 24px
                line-height: 48px
              .icon-thumb_up
                color:rgb(0,160,220)
              .icon-thumb_down
                color:rgb(147,153,159)
          .no-rating
            padding: 32px 0
            font-size: 24px
            color:rgb(147,153,159)
    &.move-enter,&.move-leave-active
      transform:translate3d(100%,0,0)
</style>



