<template>
    <div class="ratingSelect">
        <div class="rating-type">
          <span class="block ok" :class="{'active':selectType === 2}" @click = "select(2,$event)">{{desc.all}} <span class="count">{{ratings.length}}</span></span>
          <span class="block ok" :class="{'active':selectType === 0}" @click = "select(0,$event)" >{{desc.ok}} <span class="count">{{ok.length}}</span></span>
          <span class="block no" :class="{'active':selectType === 1}" @click = "select(1,$event)">{{desc.no}} <span class="count">{{no.length}}</span></span>
        </div>
        <div class="switch" :class="{'on':onlyContent}" @click="switchContent">
            <i class="icon-check_circle"></i>
            <span class="text">只看有内容的评价</span>
        </div>
    </div>
</template>

<script>
  /* eslint-disable no-unused-vars */

  const OK = 0;
  const NO = 1;
  const ALL = 2;
  export default {
    name: 'ratingSelect',
    data () {
      return {
      }
    },
    props:{
        ratings:{
          type:Array,
          default(){
            return [];
          }
        },
      selectType:{
          type:Number,
          default: ALL
      },
      desc:{
          type:Object,
          default(){
            return{
              all: '全部',
              ok : '推荐',
              no : '吐槽'
            }
          }
      },
      onlyContent:{
          type:Boolean,
          default : false
      }
    },
    methods:{
      select(type, e){
        if(!e._constructed){
          return;
        }
        this.$emit('updateSelectType', type)
      },
      switchContent(e){
        if(!e._constructed){
          return;
        }
        this.$emit("switchOnly", !this.onlyContent)
      }
    },
    created(){
      this.$nextTick(()=>{
      })
    },
    computed:{
      ok(){
        return this.ratings.filter((rating)=>{
          return rating.rateType === OK;
        })
      },
      no(){
        return this.ratings.filter((rating)=>{
          return rating.rateType === NO;
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only  添加scoped属性限制css只能在本模板用 -->
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/css/mixin.styl"
  .ratingSelect
    .rating-type
      padding:36px 0
      margin:0 36px
      border-1px(rgba(7,17,27,.1))
      font-size:0
      .block
        display:inline-block
        padding: 16px 24px
        margin-right: 16px
        border-radius: 1px
        color:rgb(77,85,93)
        line-height: 32px
        font-size: 24px
        &.active
          color:#ffffff
        .count
          margin-left: 4px
        &.ok
          background:rgba(0,160,220,.2)
          &.active
            background:rgb(0,160,220)
        &.no
          background:rgba(77,85,93,.2)
          &.active
            background:rgb(0,160,220)
    .switch
      padding:24px 36px
      line-height: 48px
      border-bottom:1px solid rgba(7,17,27,.1)
      color:rgb(147,153,159)
      font-size:0
      &.on
        .icon-check_circle
          color:#00c850
      .icon-check_circle
        margin-right: 8px
        vertical-align:top
        font-size: 48px
      .text
        vertical-align:top
        font-size: 24px


</style>



